@import "../common/variables";
@import "../common/mixins";

.just-do-it {
  display: block;
  margin-bottom: 450px;

  @media (max-width: 767px) {
    margin-bottom: 150px;
  }
}

.just-do-it__title {
  font-size: 48px;
  font-weight: 600;
  text-align: center;
  max-width: 445px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 100px;

  @media (max-width: 767px) {
    font-size: 30px;
  }
}

.just-do-it__btn-wrap {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.just-do-it__bg-img {
  position: absolute;
  top: calc(50% + 60px);
  left: calc(50% + 40px);
  transform: translate(-50%, -50%);
  background-image: url("../media/orbit.png");
  background-repeat: no-repeat;
  width: 2311px;
  height: 924px;

  @media (max-width: 767px) {
    width: 1055px;
    height: 507px;
    background-image: url("../media/orbit-mob.png");
  }
}

.just-do-it__btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background: radial-gradient(109.71% 127.93% at 10.92% -28.55%, #FFE600 0%, #CD69EA 45.7%, #1070FF 100%);
  width: 210px;
  height: 210px;
  border-radius: 50%;
  text-decoration: unset;
  color: white;
  font-size: 24px;
  font-weight: bold;
  z-index: 1;

  @media (max-width: 767px) {
    width: 140px;
    height: 140px;
    font-size: 18px;
  }

  &::before {
    content: "";
    border: 1px solid #AABDFF;
    position: absolute;
    width: 244px;
    height: 244px;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (max-width: 767px) {
      width: 160px;
      height: 160px;
    }

  }

  &::after {
    content: "";
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 25%, rgba(255, 255, 255, 0.61973) 25%, rgba(255, 255, 255, 0.385907) 75%, rgba(255, 255, 255, 0) 75%, rgba(255, 255, 255, 0) 100%);
    position: absolute;
    width: 2px;
    height: 80px;
    top: -80px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.just-do-it__btn-img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @media (max-width: 767px) {
    width: 350px;
    height: 320px;
  }
}
